@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  display: flex;
  margin: 0;
  min-height: 100%;
  overflow-x: hidden;
  flex-direction: column;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #1c1b21;
}
.page__main {
  flex-grow: 1;
}
@media (min-width: 1110px) {
  .page {
    background-image: url("../img/hero-bg@2x.jpg");
  }
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}
.section-title {
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #ffffff;
}
@media (max-width: 980px) {
  .section-title {
    font-size: 28px;
    line-height: 42px;
  }
}
.section-title--stylized::before {
  display: inline-block;
  width: 64px;
  height: 31px;
  margin-right: 20px;
  background-image: url("../img/title-stylized.svg");
  background-size: 64px 31px;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
}
.section-text {
  color: #ffffff;
  opacity: 0.7;
}
.header {
  padding: 30px 0;
  margin-bottom: 320px;
}
.navigation {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 980px) {
  .navigation {
    justify-content: space-between;
    padding: 0 30px;
    align-items: flex-start;
  }
}
.navigation__logo {
  display: block;
  margin-right: 65px;
}
@media (max-width: 980px) {
  .navigation__logo {
    margin-right: 0;
  }
}
.navigation__toggle {
  position: absolute;
  top: 14px;
  right: 30px;
  display: none;
}
@media (max-width: 980px) {
  .navigation__toggle {
    display: block;
  }
}
.navigation__link {
  font-size: 18px;
  line-height: 27px;
  font-weight: 600;
  text-decoration: none;
  color: #ffffff;
  transition: 0.3s;
}
.navigation__link:hover {
  color: #7a66cc;
}
.navigation__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  transition: 0.3s;
}
@media (max-width: 980px) {
  .navigation__list {
    width: 100%;
    transform: translate(100vw, 6vh);
    flex-direction: column;
    align-items: center;
    row-gap: 6vh;
  }
  .navigation__list--opened {
    transform: translate(0, 6vh);
  }
}
@media (max-width: 420px) {
  .navigation__list {
    row-gap: 2em;
  }
}
.navigation__item {
  margin-right: 56px;
}
@media (max-width: 980px) {
  .navigation__item {
    margin-right: 0;
  }
}
.navigation-toggle {
  background-color: transparent;
  border: none;
  height: 30px;
  padding: 0;
  cursor: pointer;
}
.navigation-toggle:hover {
  opacity: 0.6;
}
.navigation-toggle:active {
  opacity: 0.3;
}
.navigation-toggle__line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 10px 0;
  background-color: #ffffff;
}
.navigation-toggle__line::before {
  position: absolute;
  top: 10px;
  left: 0;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 10px 0;
  content: "";
}
.navigation-toggle__line::after {
  position: absolute;
  bottom: 10px;
  left: 0;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 10px 0;
  content: "";
}
.section {
  max-width: 1150px;
  width: 100%;
  padding: 0 20px;
  padding-bottom: 100px;
  margin: 0 auto;
}
.hero {
  padding: 0 20px;
  padding-bottom: 130px;
}
.hero__container {
  display: flex;
  max-width: 1110px;
  margin: 0 auto;
  flex-direction: column;
  text-transform: capitalize;
}
@media (max-width: 420px) {
  .hero__container {
    padding: 0 1em;
  }
}
.hero__single-name {
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  color: transparent;
  -webkit-text-stroke-color: #ffffff;
  -webkit-text-stroke-width: 1px;
}
@media (max-width: 980px) {
  .hero__single-name {
    font-size: 50px;
    line-height: 100px;
  }
}
.hero__audio {
  width: 100%;
}
@media (max-width: 420px) {
  .logo__text {
    display: none;
  }
}
.news-list {
  display: flex;
  max-width: 1110px;
  margin: 0 auto;
  padding: 0;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style-type: none;
}
@media (max-width: 769px) {
  .news-list {
    justify-content: center;
  }
}
.news-item {
  position: relative;
}
.news-item:not(:last-of-type) {
  margin-right: 30px;
}
@media (max-width: 980px) {
  .news-item:not(:last-of-type) {
    margin-bottom: 30px;
    margin-right: 0;
  }
}
@media (min-width: 740px) and (max-width: 1150px) {
  .news-item:not(:last-of-type) {
    margin-right: 30px;
    margin-bottom: 30px;
  }
}
.news-item__wrapper {
  max-width: 350px;
}
.news-item__wrapper:hover .news-item__text {
  max-height: 300px;
  height: 100%;
}
.news-item__image {
  display: block;
  border-radius: 20px 0;
}
@media (max-width: 420px) {
  .news-item__image {
    max-width: 300px;
    height: auto;
  }
}
.news-item__text {
  position: absolute;
  display: flex;
  align-items: center;
  max-width: 350px;
  width: 100%;
  max-height: 90px;
  height: 100%;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 2em 1em;
  padding-right: 0;
  border-radius: 20px 0;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
  background-color: rgba(122, 102, 204, 0.7);
  transition: 0.3s ease;
}
@media (max-width: 420px) {
  .news-item__text {
    max-width: 300px;
    font-size: 14px;
    line-height: 20px;
  }
}
.about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);
  justify-content: space-between;
}
@media (max-width: 980px) {
  .about {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
@media (max-width: 980px) {
  .about__title {
    grid-column: 1;
    grid-row: 1;
  }
}
.about__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-column: 1 / 2;
  grid-row: 2 / -1;
}
@media (max-width: 980px) {
  .about__text-wrapper {
    grid-row: 2;
    margin-bottom: 40px;
  }
}
.about__text {
  margin-bottom: 60px;
}
.about__list {
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: repeat(2, 1fr);
  list-style-type: none;
  row-gap: 30px;
  column-gap: 40px;
}
@media (max-width: 420px) {
  .about__list {
    grid-template-columns: 1fr;
  }
}
.about__item {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
}
.about__item::before {
  display: block;
  width: 30px;
  height: 30px;
  margin-right: 20px;
  border-radius: 10px 0;
  background-color: #7a66cc;
  content: "";
}
.about__images-wrapper {
  display: grid;
  grid-template-columns: repeat(6, 70px);
  grid-template-rows: repeat(9, 50px);
  grid-column: -2 / -1;
  grid-row: 1 / -1;
  align-self: center;
  justify-self: end;
}
@media (max-width: 980px) {
  .about__images-wrapper {
    grid-row: 3;
    grid-template-columns: repeat(6, 40px);
    grid-template-rows: repeat(9, 30px);
    justify-self: center;
  }
}
.about__image {
  display: block;
  border-radius: 30px 0;
}
@media (max-width: 420px) {
  .about__image {
    width: 200px;
    height: auto;
  }
}
.about__image--first {
  grid-column-start: 1;
  grid-row-start: 1;
  z-index: 1;
}
.about__image--second {
  grid-column-end: 3;
  grid-row-end: 3;
  opacity: 0.5;
}
.footer {
  display: flex;
  justify-content: center;
}
.footer__logo {
  display: flex;
  align-items: center;
}
.footer__logo-face {
  margin-right: 20px;
}
